  --font-color-primary: #333;
  --font-color-white: #fff;
  --font-color-black: #000;
  --font-color-dark-blue: #13829b;
  --bg-color-light-orange: #fcc29a;
  --bg-color-very-light-orange: #fde9c9;
  --bg-color-cyan: #29d2e4;
  --bg-color-dark-cyan: #13829b;
  --font-family-primary: Lato;
  --font-family-secondary: Raleway;
}

body {
  font-size: 1rem;
  color: var(--font-color-primary, #333);
  position: relative;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 1rem;
  margin: 0;
  padding: 0;
}

ul,
ol,
li {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
}

/* Styling website primary header */
.header__content {
  display: grid;
  grid-auto-flow: column;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
  padding: 1rem 5rem;
  border-bottom: 3px solid #000;
}

.header__nav-links-list {
  display: flex;
  gap: 4rem;
  padding-top: 1rem;
}

.header__nav-link {
  font-family: var(--font-family-primary, Lato);
  font-size: 1.125rem;
  font-style: normal;
  font-weight: 400;
  color: var(--font-color-black, #000);
  text-decoration: underline;
  text-decoration-color: #00000000;
  line-height: normal;
  padding: 0.425rem;
  border-radius: 0.5rem;
  transition: text-decoration-color 250ms;
  text-decoration-thickness: 0.625rem;
  text-underline-offset: 1.9rem;
}

.header__nav-link:hover {
  text-decoration-color: #000000ff;
}

.header__nav-link:active {
  transition: text-underline-offset 100ms ease;
  text-underline-offset: 0.5rem;
}

.header__nav--secondary {
  align-self: flex-end;
}

.header__nav-button {
  font-family: var(--font-family-primary, Lato);
  font-size: 1rem;
  font-style: normal;
  font-weight: 600;
  color: var(--font-color-white, #fff);
  line-height: normal;
  padding: 0.75rem 2rem;
  background-color: var(--bg-color-dark-cyan, #13829b);
  border: none;
  border-radius: 0.5rem;
}

.button--click-animation {
  transition: transform 100ms;
}

.button--click-animation:hover {
  transform: scale(1.05);
}

.button--click-animation:active {
  transform: scale(1);
}

/* Styling main section */
.donate-section {
  display: grid;
  grid-auto-flow: column;
  grid-template-columns: 1fr 1.2fr;
  justify-content: center;
  align-items: center;
  margin-bottom: 15rem;
  padding-left: 5rem;
}

.donate-section__text-content {
  position: relative;
  z-index: 1;
}

.donate-section__tag-list {
  list-style-position: inside;
  list-style-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

.donate-section__tag-list-element {
  font-family: Lato;
  font-size: 1.125rem;
  font-style: normal;
  font-weight: 400;
  color: var(--bg-color-dark-cyan, #13829b);
  line-height: normal;
}

.donate-section__title {
  font-family: var(--font-family-secondary, Raleway);
  font-size: 4rem;
  font-style: normal;
  font-weight: 700;
  color: var(--font-color-primary, #333);
  line-height: normal;
  position: relative;
  margin-bottom: 2.75rem;
  max-width: 90%;
}

.donate-section__title--highlighted {
  font-family: var(--font-family-secondary, Raleway);
  font-size: 4rem;
  font-style: normal;
  font-weight: 700;
  color: var(--bg-color-dark-cyan, #13829b);
  text-decoration: underline;
  text-decoration-color: var(--bg-color-very-light-orange, #fde9c9);
  line-height: normal;
