  font-style: normal;
  font-weight: 400;
  color: var(--font-color-primary, #333);
  line-height: 1.8125rem; /* 161.111% */
  margin-bottom: 2.5rem;
  max-width: 70%;
}

.donate-section__support-block {
  display: flex;
  gap: 4rem;
  align-items: center;
}

.donate-section__button {
  font-family: var(--font-family-primary, Lato);
  font-size: 1.25rem;
  font-style: normal;
  font-weight: 600;
  color: var(--font-color-white, #fff);
  line-height: normal;
  gap: 1rem;
  padding: 1.5rem 3.5rem;
  background-color: var(--bg-color-dark-cyan, #13829b);
  border: none;
  border-radius: 0.5rem;
}

.donate-section__support-block--extra {
  text-decoration: none;
  display: flex;
  gap: 1rem;
  align-items: center;
}

.donate-section__support-block-icon {
  display: block;
  width: 3rem;
  height: 3rem;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
}

.donate-section__support-block-title {
  font-family: var(--font-family-primary, Lato);
  font-size: 1.125rem;
  font-style: normal;
  font-weight: 600;
  color: var(--font-color-primary, #333);
  line-height: 1.8125rem;
}

.donate-section__image-content {
  display: grid;
  position: relative;
  justify-content: right;
}

.donate-section__image-content--kids-image {
  position: absolute;
  z-index: 3;
  top: 8rem;
  right: 65%;
  border: 5px solid white;
  border-radius: 15rem;
  filter: grayscale(100%);
}

.donate-section__image-content-block--clothes-image {
  position: relative;
  z-index: 1;
}

.donate-section__image-content-square--blue-transparent {
  position: absolute;
  bottom: 15%;
  left: 15%;
  width: 30%;
  height: 27%;
  background-color: var(--bg-color-cyan, #29d2e4);
  mix-blend-mode: multiply;
}

.donate-section__image-content-rectangle {
  position: absolute;
  z-index: 0;
  right: 0;
  bottom: -5rem;
  width: 100%;
  height: 19rem;
  background-color: var(--bg-color-light-orange, #fcc29a);
}

/* Second section of website styling */
.additional-info-section__content {
  display: grid;
  grid-template-columns: repeat(auto-fit, 400px);
  justify-content: center;
  align-items: center;
  margin-bottom: 14rem;
}

.additional-info-section__content-item {
  display: grid;
  justify-items: center;
  border-right: 0.3rem solid #fcc29a;
  padding: 1rem;
}

.additional-info-section__content-item:last-child {
  border: none;
}

.additional-info-section__content-item-icon {
  margin-bottom: 1.7rem;
  width: 3.0625rem;
  height: 2.875rem;
}

.additional-info-section__content-item-title {
  font-family: var(--font-family-secondary, Raleway);
  font-size: 1.5rem;
  font-style: normal;
  font-weight: 600;
  color: var(--font-color-primary, #333);
  line-height: normal;
  margin-bottom: 1.25rem;
  text-align: center;
}

.additional-info-section__content-item-link {
  text-decoration: none;
  display: flex;
  gap: 0.5rem;
}

.additional-info-section__content-item-link:hover {
  text-decoration: underline;
  text-underline-offset: 0.3rem;
  text-decoration-thickness: 0.2rem;
  text-decoration-color: var(--font-color-primary, #333);
}

.additional-info-section__content-item-link-title {
  font-family: var(--font-family-secondary, Raleway);
  font-size: 1rem;
  font-style: normal;
  font-weight: 600;
  color: var(--font-color-primary, #333);
  text-transform: uppercase;
  line-height: normal;
}

.additional-info-section__content-item-link-icon {
  width: 1.125rem;
  height: 1.125rem;
}

@media screen and (max-width: 1600px) {
  /* Additional-section media querry*/
  .additional-info-section__content-item:nth-child(3) {
    border-right: none;
  }
  .additional-info-section__content-item:last-child {
    grid-row: 2/3;
    grid-column: 2/3;
  }
}

@media screen and (max-width: 1200px) {
  /* Donate Section(Main block) media query */
  .donate-section {
    grid-auto-flow: row;
    grid-gap: 1.5rem;
    grid-template-columns: auto;
    margin-bottom: 5rem;
  }

  .donate-section__title-block-icon {
    right: 0;
  }

  .donate-section__image-content--kids-image {
    top: 30%;
    left: 0;
  }
  .donate-section__image-content-rectangle {
    bottom: 30%;
    width: 100%;
    height: 20%;
  }

  /* Additional-section(Second section) media query */
  .additional-info-section__content {
    grid-gap: 1rem;
    margin-bottom: 7rem;
  }
  .additional-info-section__content-item:nth-child(2) {
    border-right: none;
  }
  .additional-info-section__content-item:nth-child(3) {
    border-right: 0.3rem solid #fcc29a;
  }
  .additional-info-section__content-item:last-child {
    grid-row: auto;
    grid-column: auto;
  }
}

@media screen and (max-width: 900px) {
  /* Primary header media query */
  .header__content {
    padding: 1rem;
  }
}

@media screen and (max-width: 768px) {
  /* Donate Section(Main block) media query */
  .donate-section__title {
    margin-bottom: 0;
  }
  .donate-section__title-block-icon {
    position: relative;
    margin-left: 90%;
  }
  .donate-section {
    padding-left: 1rem;
  }
  .donate-section__support-block {
    gap: 1rem;
  }

  /* Additional-section(Second section) media query */
  .additional-info-section__content {
    justify-items: center;
  }
  .additional-info-section__content-item {
    border-right: none;
    border-bottom: 0.3rem solid #fcc29a;
    border-radius: 2rem;
  }
  .additional-info-section__content-item:nth-child(3) {
    border-right: none;
  }
  .additional-info-section__content-item:last-child {
    border-bottom: 0.3rem solid #fcc29a;
    border-radius: 2rem;
  }
}
